<template>
  <div>
    <div class="banner">
      <img src="./assets/img/bg/bg_01.png" alt="" />
      <a class="btn" href="javascript:;" @click="openMywinning"></a>
    </div>
    <div class="lottery">
      <img class="bg" src="./assets/img/bg/bg_02.png" alt="" />
      <div class="lottery-item">
        <div class="lottery-start">
          <div class="box gray" v-if="isStart === 0">
            <p>活动未开始</p>
          </div>
          <div class="box" @click="startLottery" v-if="isStart === 1">
            GO
          </div>
          <div class="box gray" v-if="isStart === 2">
            <p>活动已过期</p>
          </div>
        </div>
        <ul>
          <transition-group name="flip-list" tag="ul">
            <li
              v-for="(item, i) in list"
              :class="i == index ? 'on' : ''"
              :key="item.id"
            >
              <div class="box">
                <p><img :src="images[item.id - 1]" alt="" /></p>
                <p>{{ item.title }}</p>
              </div>
            </li>
          </transition-group>
        </ul>
      </div>
    </div>
    <div class="draw-num">
      <img class="bg" src="./assets/img/bg/bg_03.png" alt="" />
      <div class="txt">
        今天还有 <span>{{ countstart }}</span> 次机会
      </div>
    </div>
    <winninglist />
    <description />
    <div class="footer">
      <img class="bg" src="./assets/img/bg/bg_05.png" alt="" />
    </div>
    <transition name="fade">
      <div class="mask" v-if="showToast || showMywinning"></div>
    </transition>
    <transition name="fade-top">
      <div class="lottery-alert" v-if="showToast">
        <p><img :src="images[list[index].id - 1]" alt="" /></p>
        <h2>恭喜您获得{{ list[index].title }}</h2>
        <div class="btn primary" @click="showToast = false">再来一次</div>
        <div class="btn" @click="showToast = false">关闭</div>
      </div>
    </transition>
    <mywinning :visible.sync="showMywinning" />
  </div>
</template>

<script>
import { shuffle } from "./util";
import winninglist from "./components/winninglist";
import description from "./components/description";
import mywinning from "./components/mywinning";
const images = [
  require("./assets/img/prize/1.png"),
  require("./assets/img/prize/2.png"),
  require("./assets/img/prize/3.png"),
  require("./assets/img/prize/4.png"),
  require("./assets/img/prize/5.png"),
  require("./assets/img/prize/6.png"),
  require("./assets/img/prize/7.png"),
  require("./assets/img/prize/8.png")
];
function bodyfixed(val) {
  if (val) document.body.style.overflow = "hidden";
  else document.body.style.overflow = "auto";
}
export default {
  name: "app",
  components: { description, winninglist, mywinning },
  data: function() {
    return {
      isStart: 1,
      score: 10,
      images,
      list: [
        {
          id: 1,
          title: "一等奖"
        },
        {
          id: 2,
          title: "二等奖"
        },
        {
          id: 6,
          title: "六等奖"
        },
        {
          id: 3,
          title: "三等奖"
        },
        {
          id: 8,
          title: "八等奖"
        },
        {
          id: 4,
          title: "四等奖"
        },
        {
          id: 7,
          title: "七等奖"
        },
        {
          id: 5,
          title: "五等奖"
        }
      ],
      index: -1,
      // index: 0,
      count: 8,
      countstart: 0,
      speed: 200,
      times: 0,
      cycle: 50,
      prize: -1,
      drawing: false,
      showToast: false,
      showMywinning: false
    };
  },
  watch: {
    showToast(val) {
      bodyfixed(val);
    },
    showMywinning(val) {
      bodyfixed(val);
    }
  },
  created() {},
  mounted() {
    setTimeout(() => {
      this.list = shuffle(this.list);
      this.runCount();
    }, 400);
  },
  methods: {
    runCount() {
      setTimeout(() => {
        this.countstart += 1;
        if (this.countstart < this.count) this.runCount();
      }, 400 / this.count);
    },
    startLottery() {
      if (this.drawing && this.count === 0) {
        return;
      }
      this.drawing = true;
      this.startRoll();
    },
    startRoll() {
      this.times += 1;
      this.oneRoll();
      if (this.times > this.cycle + 10 && this.prize === this.index) {
        clearTimeout(this.timer);
        this.prize = -1;
        this.times = 0;
        this.speed = 200;
        this.drawing = false;
        this.countstart -= 1;
        setTimeout(() => {
          this.showToast = true;
        }, 500);
      } else {
        if (this.times < this.cycle) {
          this.speed -= 10;
        } else if (this.times === this.cycle) {
          this.prize = parseInt(
            Math.random() * (this.count - 1 - 0 + 1) + 0,
            10
          );
        } else if (
          this.times > this.cycle + 10 &&
          ((this.prize === 0 && this.index === 7) ||
            this.prize === this.index + 1)
        ) {
          this.speed += 110;
        } else {
          this.speed += 20;
        }
        if (this.speed < 40) {
          this.speed = 40;
        }
        this.timer = setTimeout(this.startRoll, this.speed);
      }
    },
    oneRoll() {
      let index = this.index;
      const count = this.count;
      index += 1;
      if (index > count - 1) {
        index = 0;
      }
      this.index = index;
    },
    openMywinning() {
      this.showMywinning = true;
    }
  }
};
</script>

<style lang="scss"></style>
